@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;800&display=swap);
body,html{margin:0;font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.slide-in-menu{display:flex;flex-direction:column;width:100vw;height:100vh}.slide-in-menu .slide-in-nav{background-color:#28292b;display:flex;flex-direction:column;justify-content:space-between;align-items:baseline;letter-spacing:.1em;text-transform:uppercase;font-weight:500;width:80vw;height:100vh;padding:20px 0}.slide-in-menu .slide-in-nav .slide-in-nav-group{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start;padding:10px}
.burger-container{display:inline-block;cursor:pointer;position:relative;top:15px;right:25px}.burger-container .bar1,.burger-container .bar2,.burger-container .bar3{width:34px;height:4px;background-color:#fff;margin:7px 0;transition:.4s}.burger-container .change.bar1{-webkit-transform:rotate(-45deg) translate(-9px, 6px);transform:rotate(-45deg) translate(-9px, 6px)}.burger-container .change.bar2{opacity:0}.burger-container .change.bar3{-webkit-transform:rotate(45deg) translate(-8px, -8px);transform:rotate(45deg) translate(-8px, -8px)}
.footer{background-color:#202124;height:80px;display:flex;justify-content:space-evenly;align-items:center;width:100%;color:#fff;text-align:center;margin-top:30px}.footer .social-section{align-items:stretch;display:flex;justify-content:space-evenly;list-style-type:none;text-align:center;padding:0;margin:0}.footer .social-section .social{width:40px}.footer .social>a>img{border-radius:50%;transition:all,.5s;border:none;object-fit:cover;width:30px}.footer .social>a>img:hover{background-color:#b3b4b6;-webkit-transform:scale(1.2);transform:scale(1.2)}
.app{background-image:url(/PANTRI/static/media/background-dark.654cf2d9.png);background-color:#202124;background-repeat:repeat;background-size:40vmin;display:flex;flex-direction:column;justify-content:space-between;min-height:100vh}.app .app-header{background-color:#202124;min-height:80px;padding:10px 0;width:100%}@media screen and (max-width: 575px){.app .app-header{position:fixed;z-index:998}}.app .app-header .nav{align-items:center;display:flex;flex-wrap:nowrap;justify-content:space-evenly}.app .app-header .nav .logo{width:250px}.app .app-header .nav .nav-group{display:flex;flex-wrap:wrap;justify-content:center}.app .app-header .small-screen{display:flex;flex-wrap:nowrap;justify-content:space-between;width:100%}.app .app-header .small-screen .logo{height:auto;margin:0 10px;width:200px}.nav-link{color:#fff;font-weight:600;letter-spacing:.1em;margin:0 10px;padding:.5rem;text-align:center;text-decoration:none;text-transform:uppercase;transition:all .5s;white-space:nowrap;cursor:pointer}.nav-link:hover{color:rgba(255,255,255,.5);-webkit-transform:scale(1.15);transform:scale(1.15)}a{color:#fff;text-decoration:none;transition:all .5s}a:hover{color:rgba(255,255,255,.5);-webkit-transform:scale(1.15);transform:scale(1.15)}.error{color:#b90000}*{box-sizing:border-box}.body{flex-grow:1}.fade-in{animation:fadeIn ease .8s;-webkit-animation:fadeIn ease .8s;-moz-animation:fadeIn ease .8s;-o-animation:fadeIn ease .8s;-ms-animation:fadeIn ease .8s;transition:all .8}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.page{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%}.page .page-header{margin-top:10px;padding:10px;background-color:#202124;max-width:500px;width:100%;color:#b3b4b6;border:1.5px solid #404143;border-radius:6px;box-shadow:4px 4px 8px 0px rgba(0,0,0,.4)}.page .recipes-empty{align-items:center;background-color:#202124;border-radius:4px;box-shadow:4px 4px 8px 0px rgba(0,0,0,.2);display:flex;flex-direction:column;margin:20px 20px}.page .title{align-items:center;background-color:#202124;color:#fff;border-radius:4px;box-shadow:4px 4px 8px 0px rgba(0,0,0,.2);display:flex;font-weight:600;letter-spacing:.07em;text-transform:uppercase;justify-content:center;padding:15px 0;text-align:center;width:100%;margin:10px 0}@media screen and (max-width: 575px){.page .title{margin-top:100px}}.page .new-recipe-title{font-weight:600;letter-spacing:.07em;text-transform:uppercase}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,textarea:-webkit-autofill,textarea:-webkit-autofill:hover,textarea:-webkit-autofill:focus,select:-webkit-autofill,select:-webkit-autofill:hover,select:-webkit-autofill:focus{border:none;-webkit-text-fill-color:#fff;-webkit-box-shadow:0 0 0px 1000px #202124 inset;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}
